import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import { artCycleService, bloodTestsService, semenService } from "./service";

// css
import "primevue/resources/themes/saga-blue/theme.css";
import "primevue/resources/primevue.min.css";
import "primeicons/primeicons.css";
import "primeflex/primeflex.css";
import "./style.css";

import PrimeVue from "primevue/config";

// form imports
import AutoComplete from "primevue/autocomplete";
import Calendar from "primevue/calendar";
import Checkbox from "primevue/checkbox";
import Dropdown from "primevue/dropdown";
import InputMask from "primevue/inputmask";
import InputNumber from "primevue/inputnumber";
import InputText from "primevue/inputtext";
import Listbox from "primevue/listbox";
import MultiSelect from "primevue/multiselect";
import Password from "primevue/password";
import RadioButton from "primevue/radiobutton";
import Textarea from "primevue/textarea";
import ToggleButton from "primevue/togglebutton";

// button imports
import Button from "primevue/button";

// data imports
import DataTable from "primevue/datatable";
import Column from "primevue/column";
import ColumnGroup from "primevue/columngroup";
import Row from "primevue/row";

// panel imports
import Card from "primevue/card";
import Divider from "primevue/divider";
import Fieldset from "primevue/fieldset";
import Panel from "primevue/panel";
import ScrollPanel from "primevue/scrollpanel";
import Toolbar from "primevue/toolbar";

// overlay imports
import Dialog from "primevue/dialog";
import OverlayPanel from "primevue/overlaypanel";

// menu imports
import TabMenu from "primevue/tabmenu";

// message imports
import Message from "primevue/message";
import Toast from "primevue/toast";
import ToastService from "primevue/toastservice";

// misc imports
import Avatar from "primevue/avatar";
import ProgressSpinner from "primevue/progressspinner";
import BlockUI from "primevue/blockui";

const app = createApp(App);
app.use(store)
    .use(router)
    .use(PrimeVue)
    .use(ToastService)
    .mount("#app");

// Register service provide

app.provide("artCycleService", artCycleService);
app.provide("bloodTestsService", bloodTestsService);
app.provide("semenService", semenService);

// register form components
app.component("PAutoComplete", AutoComplete);
app.component("PCalendar", Calendar);
app.component("PCheckbox", Checkbox);
app.component("PDropdown", Dropdown);
app.component("PInputMask", InputMask);
app.component("PInputNumber", InputNumber);
app.component("PInputText", InputText);
app.component("PMultiSelect", MultiSelect);
app.component("PListbox", Listbox);
app.component("PPassword", Password);
app.component("PRadioButton", RadioButton);
app.component("PTextarea", Textarea);
app.component("PToggleButton", ToggleButton);

// Register button components
app.component("PButton", Button);

// Register data components
app.component("PDataTable", DataTable);
app.component("PColumn", Column);
app.component("PColumnGroup", ColumnGroup);
app.component("PRow", Row);

// Register panel components
app.component("PCard", Card);
app.component("PDivider", Divider);
app.component("PFieldset", Fieldset);
app.component("PPanel", Panel);
app.component("PScrollPanel", ScrollPanel);
app.component("PToolbar", Toolbar);

// Register overlay components
app.component("PDialog", Dialog);
app.component("POverlayPanel", OverlayPanel);

// Register menu components
app.component("PTabMenu", TabMenu);

// Register message compontest
app.component("PMessage", Message);
app.component("PToast", Toast);

// Register misc components
app.component("PAvatar", Avatar);
app.component("PProgressSpinner", ProgressSpinner);
app.component("PBlockUi", BlockUI);
