import { BASE_URL } from '../config.js';
import axios from 'axios';
import { authoreizeAxios } from './app-http'

export default {
    login(user) {
        return axios
            .post(`${BASE_URL}/login`, {
                username: user.username,
                password: user.password
            })
            .then(resp => {
                localStorage.setItem('user', JSON.stringify({
                    user: user.username,
                    accessToken: resp.data.api_key
                }));

                authoreizeAxios();

                return {
                    user: user.username,
                    accessToken: resp.data.api_key
                }
            })
    },

    logout() {
        localStorage.removeItem('user');
    },

    register(user) {
        return axios.post(BASE_URL + 'signup', {
            username: user.username,
            email: user.email,
            password: user.password
        });
    },
}