import { $axios } from "@/service/app-http";

const OPU_ENDPOINT = "opu/by-cycle-id";
const SAVE_OPU_ENDPOINT = "opu/save-opu";

export async function getOPUSummary(cycleId) {
    const {
        data: { data }
    } = await $axios.get(OPU_ENDPOINT, {
        params: { cycleId }
    });
    return data;
}

export async function saveOpuSummary(formData) {
    const { data } = await $axios.post(SAVE_OPU_ENDPOINT, formData);
    return data;
}
