import * as artCycleService from "@/service/art-cycle";
import { emitter } from "@/event-bus";

const artCycle = {
    namespaced: true,
    state: () => ({
        cyclesSummary: [],
        patient: null,
        partner: null,
        cycleTypes: null,
        selectedCycle: null,
        selectedCycleNo: null,
        cycleTypeOptions: null,
        spermSourceOptions: null,
        eggSourceOptions: null
    }),
    mutations: {
        SET_SELECTED_CYCLE(state, payload) {
            state.selectedCycle = payload;
        },
        SET_SELECTED_CYCLE_NO(state, payload) {
            state.selectedCycleNo = payload;
        },
        SET_SELECTED_CYCLE_TO_FIRST(state) {
            let index = state.cyclesSummary.findIndex(
                (item) => item.cycleNo == state.cyclesSummary.length
            );
            state.selectedCycle = state.cyclesSummary[index];
        }
    },
    getters: {
        getSelectedCycle: ({ cyclesSummary, selectedCycleNo }) => {
            let index = cyclesSummary.findIndex(
                (item) => item.cycleNo === selectedCycleNo
            );
            if (index != -1) {
                return cyclesSummary[index];
            } else {
                return null;
            }
        }
    },
    actions: {
        async getCyclesDetails({ state }, demographicNo) {
            try {
                const {
                    petientDetails,
                    partnerDetails,
                    cyclesSummery,
                    cycleTypes
                } = await artCycleService.getCycleSummaryDetail(demographicNo);

                state.cyclesSummary = cyclesSummery.map((item) => ({
                    ...item,
                    eggDonor: item.eggDoner
                }));
                state.patient = petientDetails;
                state.partner = partnerDetails;
                state.cycleTypeOptions = cycleTypes;

                emitter.emit("artCycleStore:detail-loaded", cyclesSummery);

                if (cyclesSummery) {
                    Promise.resolve();
                }
            } catch (error) {
                return Promise.reject();
            }
        },
        updateSelectedCycle({ state }, cycle) {
            state.selectedCycle = cycle;
        },
        updateCycle({ state }, cycle) {
            let index = state.cyclesSummary.findIndex(
                (item) => item.cycleId === cycle.cycleId
            );
            Object.assign(state.cyclesSummary[index], {
                ...cycle,
                cycleCompleted: cycle.complete
            });
        },
        nextCycle({ state, commit }) {
            let index = state.cyclesSummary.findIndex(
                (item) => item.cycleNo === state.selectedCycle.cycleNo + 1
            );
            if (index != -1) {
                commit("SET_SELECTED_CYCLE", state.cyclesSummary[index]);
            }
        },
        previsouCycle({ state, commit }) {
            let index = state.cyclesSummary.findIndex(
                (item) => item.cycleNo === state.selectedCycle.cycleNo - 1
            );
            if (index != -1) {
                commit("SET_SELECTED_CYCLE", state.cyclesSummary[index]);
            }
        },
        async getCycleFormFieldOptions({ state }) {
            if (
                !state.cycleTypeOptions ||
                !state.spermSourceOptions ||
                !state.eggSourceOptions
            ) {
                const {
                    spermSources,
                    eggSource
                } = await artCycleService.getFormFieldOptions();

                state.spermSourceOptions = spermSources;
                state.eggSourceOptions = eggSource;
            }
        },
        async createNewCycle({ state }) {
            const newCycle = {
                patientId: state.patient.id,
                cycleNo: state.cyclesSummary.length + 1,
                cycleType: "IVF",
                cycleNoType: 1,
                spermSource: "Parnter",
                eggSource: "Own",
                cycleDate: "2021-05-06",
                cycleSucceeded: true,
                cycleExpired: false,
                eggDonor: true
            };

            try {
                const { data } = await artCycleService.createNewCycle(newCycle);
                const cycle = { ...newCycle, ...data };
                state.cyclesSummary.push(cycle);
                state.selectedCycle = cycle;
                if (data) {
                    return Promise.resolve();
                }
            } catch (err) {
                console.log(err);
                return Promise.reject();
            }
        },
        async deleteCycle({ state, commit }, cycelId) {
            try {
                const {
                    data: { data }
                } = await artCycleService.deleteCycle(cycelId);

                if (data) {
                    let index = state.cyclesSummary.findIndex(
                        (item) => item.cycleId === data.cycleId
                    );
                    state.cyclesSummary.splice(index);
                    commit("SET_SELECTED_CYCLE_TO_FIRST");
                    return Promise.resolve();
                }
            } catch (error) {
                return Promise.reject();
            }
        }
    }
};

export { artCycle };
