import { createRouter, createWebHashHistory } from 'vue-router'
import DefaultLayout from '@/components/layouts/defalut.vue'

const routes = [
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '',
                redirect: '/login'
            },
            {
                path: 'art-cycle',
                name: 'ArtCycle',
                component: () => import('@/views/art-cycle.vue')
            },
            {
                path: 'fertilization',
                name: 'Fertilization',
                component: () => import('@/views/fertilization.vue')
            },
            {
                path: 'cycle-monitoring',
                name: 'CycleMonitoring',
                component: () => import('@/views/cyccle-monitoring.vue')
            },
            {
                path: 'opu',
                name: 'OPU',
                component: () => import('@/views/opu.vue')
            },
            {
                path: 'semen',
                name: 'Semen',
                component: () => import('@/views/semen.vue')
            },
            {
                path: 'semen-cryo-storage',
                name: 'SemenCryoStorage',
                component: () => import('@/views/semen-cryo-storage.vue')
            },
            {
                path: 'pregnancy',
                name: 'Pregnancy',
                component: () => import('@/views/pregnancy.vue')
            },
            {
                path: 'embryo-transfer',
                name: 'EmbryoTransfer',
                component: () => import('@/views/embryo-transfer.vue')
            },
            {
                path: 'cryo-storage',
                name: 'CryoStorage',
                component: () => import('@/views/cryo-storage.vue')
            },
            {
                path: 'blood-tests',
                name: 'BloddTests',
                component: () => import('@/views/blodd-tests.vue')
            },
            {
                path: 'daily-report',
                name: 'DailyReport',
                component: () => import('@/views/daily-report.vue')
            },
            {
                path: 'cycle-report',
                name: 'CycleReport',
                component: () => import('@/views/cycle-report.vue')
            },
            {
                path: 'patient-message-log',
                name: 'PatientMessageLog',
                component: () => import('@/views/patient-message-log.vue')
            },
            {
                path: 'message-inbox',
                name: 'MessageInbox',
                component: () => import('@/views/message-inbox.vue')
            },
            {
                path: 'born',
                name: 'Born',
                component: () => import('@/views/born.vue')
            },
            {
                path: 'test',
                name: 'test',
                component: () => import('@/views/test.vue')
            }
        ]
    },
    {
        path: '/login',
        component: () => import('@/views/login.vue'),
        name: 'Login'
    },
    {
        path: '/:catchAll(.*)',
        component: () => import('@/views/not-found.vue')
    }
];


const router = createRouter({
    history: createWebHashHistory(process.env.BASE_URL),
    routes
})

router.beforeEach((to, from, next) => {
    const isAuthenticated = localStorage.getItem('user')

    if (to.name !== 'Login' && !isAuthenticated) next({ name: 'Login' })
    else next()
})

export default router
