import { $axios } from "./app-http";

const DEMOGRAPHIC_SEARCH_ENDPOINT = "demographics/search";
const FORM_FIELD_OPTIONS_ENDOINT = "art-cycle/field-options";
const PATIENT_CYCLES_ENDPOINT = "art-cycle/cycles";
const CYCLE_SUMMARY_DETAIL = "cycles/summary-details";
const CYCLE_CREATE_ENDPOINT = "cycle/create-new";
const CYCLE_DELETE_ENDPOINT = "cycle/delete-cycle";

export default {
    async searchDemographic({ term, sex }) {
        let limit = 10;
        return await $axios.get(DEMOGRAPHIC_SEARCH_ENDPOINT, {
            params: {
                term: term,
                sex: sex
            }
        });
    },
    async getFormFieldOptions() {
        return await $axios.get(FORM_FIELD_OPTIONS_ENDOINT);
    },
    async getCycles(patientId) {
        return await $axios.get(PATIENT_CYCLES_ENDPOINT, {
            params: {
                "patient-id": patientId
            }
        });
    },
    async getCycleSummaryDetail(patientId) {
        return await $axios.get(CYCLE_SUMMARY_DETAIL, {
            params: {
                patientId
            }
        });
    }
};

export async function searchDemographic({ term, sex }) {
    const data = await $axios.get(DEMOGRAPHIC_SEARCH_ENDPOINT, {
        params: {
            term: term,
            sex: sex
        }
    });

    return data;
}

export async function getFormFieldOptions() {
    const { data } = await $axios.get(FORM_FIELD_OPTIONS_ENDOINT);
    return data;
}

export async function getCycleSummaryDetail(patientId) {
    const {
        data: { data }
    } = await $axios.get(CYCLE_SUMMARY_DETAIL, {
        params: {
            patientId
        }
    });

    return data;
}

export async function createNewCycle(cycle) {
    const { data } = await $axios.post(CYCLE_CREATE_ENDPOINT, cycle);
    return data;
}

export async function deleteCycle(cycleId) {
    return await $axios.post(CYCLE_DELETE_ENDPOINT, {
        cycleId: parseInt(cycleId)
    });
}
