<template>
    <span class="p-ml-2 p-mr-2"> Cycle: </span>
    <span style="color: #f44336">{{ cycleId ? cycleId : "--------" }}</span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">{{ cycleType ? cycleType : "-----" }}</span>
    <p-divider layout="vertical" />
    <span class="p-mr-2"> Patient: </span>
    <span style="color: #f44336">{{
        patientDispaly ? patientDispaly : "----------"
    }}</span>
    <p-divider layout="vertical" />
    <span class="p-mr-2">Demographic No: </span>
    <span style="color: #f44336">
        {{ demographicNo ? demographicNo : "--------" }}
    </span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">
        {{ dateOfBirth ? dateOfBirth : "----/--/--" }}
    </span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">({{ age ? age : "--" }} yrs)</span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">{{ doctor ? doctor : "--" }}</span>
</template>

<script>
import { computed, ref, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();

        const cycleId = computed(() =>
            store.state.artCycle.selectedCycle
                ? store.state.artCycle.selectedCycle.cycleId
                : null
        );
        const cycleType = computed(() =>
            store.state.artCycle.selectedCycle
                ? store.state.artCycle.selectedCycle.cycleType
                : null
        );
        const patientDispaly = ref();
        const demographicNo = ref();
        const dateOfBirth = ref();
        const doctor = ref();
        const age = ref();

        const patient = computed(() => store.state.artCycle.patient);

        watch(patient, (newValue) => {
            patientDispaly.value = `${newValue.firstName} ${newValue.lastName}`;
            demographicNo.value = newValue.chart;
            doctor.value = newValue.doctor;
            age.value = newValue.age;
            dateOfBirth.value = `${newValue.year}-${newValue.month}-${newValue.day}`;
        });

        return {
            cycleId,
            cycleType,
            patientDispaly,
            demographicNo,
            dateOfBirth,
            age,
            doctor
        };
    }
};
</script>
