import * as monitoringService from "@/service/cycle-monitoring";

const state = () => ({
    diagnosisOptions: null,
    protocolOptions: null,
    drugs1Options: null,
    drugs2Options: null,
    details: null,
    lines: null,
    comments: null,
    selectedMonitorLine: null,
    selectedComment: null
});

const mutations = {
    SET_SELECTED_MONITOR_LINE(state, payload) {
        state.selectedMonitorLine = payload;
    },
    SET_SELECTED_COMMMENT(state, payload) {
        state.selectedComment = payload;
    },
};

const actions = {
    async getCycleMonitoringDetails({ state }, cycleId) {
        try {
            const {
                monitorDetails,
                monitorLines,
                monitorLineComments
            } = await monitoringService.getCycleMonitoringDetails(cycleId);

            state.details = monitorDetails;
            state.lines = monitorLines;
            state.comments = monitorLineComments;
            state.selectedMonitorLine = null;
            state.selectedComment = null;

            if (monitorDetails) {
                Promise.resolve();
            }
        } catch (error) {
            Promise.reject();
        }
    },
    async getFormFieldOptions({ state }) {
        if (!state.diagnosisOptions) {
            const diagnosis = await monitoringService.getDaignosisOptions();
            state.diagnosisOptions = diagnosis;
        }

        if (!state.protocolOptions) {
            const protocols = await monitoringService.getProtocolOptions();
            state.protocolOptions = protocols;
        }

        if (!state.drugs1Options) {
            const drugs1 = await monitoringService.getDrug1Options();
            state.drugs1Options = drugs1;
        }

        if (!state.drugs2Options) {
            const drugs2 = await monitoringService.getDrug2Options();
            state.drugs2Options = drugs2;
        }
    },
    async createMonitorLine({ state }, formData) {
        try {
            const data = await monitoringService.createMonitorLine({
                ...formData,
                curUser: "lena"
            });
            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async editMonitorLine({ state }, formData) {
        try {
            const data = await monitoringService.updateMonitorLine({
                ...formData,
                curUser: "lena"
            });
            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async updateComment({ state }) {
        try {
            const data = await monitoringService.updateComment({
                ...state.selectedComment,
                curUser: "lena"
            });

            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async saveMonitoringSummary({ state }, formData) {
        try {
            const { data } = await monitoringService.saveMonitoringSummary(
                formData
            );
            return data;
        } catch (error) {
            console.log(error);
        }
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions
};
